import React from 'react';
import { connect } from "react-redux";
import { Helmet } from "react-helmet";
import { Card, CardBody, CardHeader, CardTitle, Row, Col } from "reactstrap"
import Breadcrumbs from "../../../../components/@vuexy/breadCrumbs/BreadCrumb"
import * as API from "../../../../utility/crud/app.crud";
import * as Date from "../../../../utility/dateAndTime";
import { toastr } from 'react-redux-toastr'
import SystemStatusTypeSearch from '../../SearchComponents/SystemStatusTypeSearch/SystemStatusTypeSearch';
import PaymentModeTable from "./Tables/PaymentModeTable";
import PaymentModeModal from "./Modals/PaymentModeModal"


class PaymentMode extends React.Component {
    constructor(props, context) {
        super(props, context);
        
        this.state = {
            validated: false,
            columns: [],
            data: [],
            filteredData: [],
            value: '',
            show: false,
            showdelete: false,
            PAYMENT_MODE_ID: 0,
            PAYMENT_MODE_NAME: '',
            SYS_STATUS_ID: 0,
            CREATEDBY: props.user.EMAIL,
            MODIFIEDBY: props.user.EMAIL,
            CREATED_DATE: Date.ShowDate(),
            MODIFIED_DATE: Date.ShowDate(),
            InsertUpdate: '',

            opensearch: false,
            SYSTEMSTATUSTYPEDESCRIPTION: '',

            searchcolumns: [],
            systemstatussearchdata: [],
            systemstatussearchFilteredData: [],
            systemstatussearchvalue: '',

        }

        this.handleShow = () => {
            this.setState({
                show: true,
                InsertUpdate: 'Insert'
            });
            this.clearData();
        }

        this.handleHide = () => {
            this.setState({
                show: false
            });
        }

        this.handleHideDelete = () => {
            this.setState({
                showdelete: false
            });
        }

        this.handleShowSearch = () => {
            this.setState({ opensearch: true });
        };

        this.handleHideSearch = () => {
            this.setState({ opensearch: false });
        };

        this.populateData();
        this.populateTypes();

    }

  
    populateData() {
        const getdata = `
            query{
                PaymentModes{
                    PAYMENT_MODE_ID
                    PAYMENT_MODE_NAME
                    SYS_STATUS_ID
                    SYSTEMSTATUS{
                        SYS_STATUS_ID
                        SYS_STATUS_NAME
                    }
                    CREATEDBY
                    MODIFIEDBY
                    CREATED_DATE
                    MODIFIED_DATE
                }
            }
        `;

        API.getData(getdata)
            .then(({ data }) => this.setState({
                data: data.data.PaymentModes
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Payment Mode Data');
                }
            })

    }

    populateTypes() {
        const getdata = `
            query {
                SystemStatusFilter (
                    CATEGORY:"SET"
                    ){
                        SYS_STATUS_ID,
                        SYS_STATUS_NAME
                }
            }
        `;

        API.getData(getdata)
        .then(({ data }) => this.setState({
            systemstatussearchdata: data.data.SystemStatusFilter
        })).catch(error => {
            if (error.response || error.request) {
                toastr.error('Error Loading Data', 'Failed to Load System Status Type Data');
            }
        })
    }

    handleSubmit = (event) => {
        event.preventDefault();
        const validate = event.currentTarget;
        if (validate.checkValidity() === false) {
            event.preventDefault();
            event.stopPropagation();
        }
        else {

            const form = `
            mutation{
                addPaymentMode(
                    PAYMENT_MODE_NAME:"${this.state.PAYMENT_MODE_NAME}",
                    SYS_STATUS_ID:${this.state.SYS_STATUS_ID},
                    CREATEDBY:"${this.state.CREATEDBY}",
                    MODIFIEDBY:"${this.state.MODIFIEDBY}",
                    CREATED_DATE:"${this.state.CREATED_DATE}",
                    MODIFIED_DATE:"${this.state.MODIFIED_DATE}"
                    ){
                        PAYMENT_MODE_ID
                }
            }
            `;
        
            const formEdit = `
            mutation{
                updatePaymentMode(
                    PAYMENT_MODE_ID:${this.state.PAYMENT_MODE_ID},
                    PAYMENT_MODE_NAME:"${this.state.PAYMENT_MODE_NAME}",
                    SYS_STATUS_ID:${this.state.SYS_STATUS_ID},
                    CREATEDBY:"${this.state.CREATEDBY}",
                    MODIFIEDBY:"${this.state.MODIFIEDBY}",
                    CREATED_DATE:"${this.state.CREATED_DATE}",
                    MODIFIED_DATE:"${this.state.MODIFIED_DATE}"
                    ){
                        PAYMENT_MODE_ID
                }
            }
            `;

            if (this.state.InsertUpdate === 'Insert') {
                API.create(form)
                    .then(() => {
                        this.clearData();
                        this.handleHide();
                        this.populateData();
                        toastr.success('Create Record', 'Record Created Successfully');
                    }).catch(error => {
                        if (error.response || error.request) {
                            toastr.error('Error Loading Data', 'Failed to Load Data');
                        }
                    })
            } else {
                API.update(formEdit)
                    .then(() => {
                        this.clearData();
                        this.handleHide();
                        this.populateData();
                        toastr.info('Update Record', 'Record Updated Successfully');
                    }).catch(error => {
                        if (error.response || error.request) {
                            toastr.error('Error Loading Data', 'Failed to Load Data');
                        }
                    })
            }
        }
        this.setState({
            validated: true
        })
    }

    handleDelete = (event) => {
        event.preventDefault();
        const formDelete = `
        mutation{
            deletePaymentMode(
                PAYMENT_MODE_ID:${this.state.PAYMENT_MODE_ID}
                ){
                    PAYMENT_MODE_ID
            }
        }
        `;

        API.remove(formDelete)
            .then(() => {
                this.handleHideDelete();
                this.populateData();
                toastr.warning('Delete Record', 'Record Deleted Successfully');
            }).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Data');
                }
            })

    }

    handleCloseSearch = (e) => {

        this.setState({
            SYS_STATUS_ID: e.SYS_STATUS_ID,
            SYSTEMSTATUSTYPEDESCRIPTION: e.SYS_STATUS_NAME,
            opensearch: false,
        });
    };

    handleEditData = (e) => {
        this.setState({
            PAYMENT_MODE_ID: e.PAYMENT_MODE_ID,
            PAYMENT_MODE_NAME: e.PAYMENT_MODE_NAME,
            SYS_STATUS_ID: e.SYS_STATUS_ID,
            SYSTEMSTATUSTYPEDESCRIPTION:e.SYSTEMSTATUS.SYS_STATUS_NAME,
            show: true,
            InsertUpdate: 'Update'
        })
    }

    handleDeleteData = (e) => {
        this.setState({
            PAYMENT_MODE_ID: e.PAYMENT_MODE_ID,
            PAYMENT_MODE_NAME: e.PAYMENT_MODE_NAME,
            showdelete: true,
            InsertUpdate: 'Delete'
        })
    }

    clearData() {
        this.setState({
            PAYMENT_MODE_NAME: '',
            SYS_STATUS_ID: '',
            SYSTEMSTATUSTYPEDESCRIPTION:'',
            validated: false
        })
    }

    handleFilter = (e) => {
        let value = e.target.value
        let data = this.state.data
        let filteredData = this.state.filteredData
        this.setState({ value });

        if (value.length) {
            filteredData = data.filter(item => {
                let startsWithCondition =
                    item.SYSTEMSTATUS.SYS_STATUS_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.PAYMENT_MODE_NAME.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.SYSTEMSTATUS.SYS_STATUS_NAME.toString().toLowerCase().includes(value.toLowerCase()) ||
                    item.PAYMENT_MODE_NAME.toString().toLowerCase().startsWith(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredData })
        }
    }

    handleSearchFilter = (e) => {
        let value = e.target.value
        let data = this.state.systemstatussearchdata
        let filteredData = this.state.systemstatussearchFilteredData
        this.setState({ systemstatussearchvalue: value });

        if (value.length) {
            filteredData = data.filter(item => {
                let startsWithCondition =
                    item.SYS_STATUS_NAME.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.SYS_STATUS_NAME.toString().toLowerCase().includes(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredData })
        }
    }

    render() {
        const { data, value, filteredData, systemstatussearchdata, systemstatussearchvalue } = this.state
        return (
            <>
                <Helmet><title>Payment Mode</title></Helmet>

                <React.Fragment>
                    <Breadcrumbs
                        breadCrumbTitle="Admin"
                        breadCrumbParent="Setup"
                        breadCrumbActive="Payment Mode"
                    />
                    <Row>
                        <Col sm="12">
                            <Card>
                                <CardHeader>
                                    <CardTitle>Payment Mode</CardTitle>
                                </CardHeader>
                                <CardBody className="rdt_Wrapper">

                                    <SystemStatusTypeSearch
                                        handleSearchSystemStatusType={this.handleCloseSearch}
                                        opensearch={this.state.opensearch}
                                        handleHideSearch={this.handleHideSearch}
                                        value={systemstatussearchvalue}
                                        data={systemstatussearchdata}
                                        handleFilter={this.handleSearchFilter}
                                        filteredData={filteredData}
                                    />

                                    <PaymentModeTable
                                        role={this.constructor.name}
                                        handleEdit={this.handleEditData}
                                        handleDelete={this.handleDeleteData}
                                        value={value}
                                        data={data}
                                        filteredData={filteredData}
                                        handleShow={this.handleShow}
                                        handleFilter={this.handleFilter}
                                    />

                                    <PaymentModeModal
                                        validated={this.state.validated}
                                        show={this.state.show}
                                        handleHide={this.handleHide}
                                        InsertUpdate={this.state.InsertUpdate}
                                        handleSubmit={this.handleSubmit}
                                        handleShowSearch={this.handleShowSearch}

                                        SystemStatusTypeDescription={this.state.SYSTEMSTATUSTYPEDESCRIPTION}
                                        handleChangeSystemStatusTypeDescription={e => this.setState({ SYSTEMSTATUSTYPEDESCRIPTION: e.target.value })}
                                        
                                        PaymentModeName={this.state.PAYMENT_MODE_NAME}
                                        changePaymentModeName={e => this.setState({ PAYMENT_MODE_NAME: e.target.value })}

                                        showdelete={this.state.showdelete}
                                        handleHideDelete={this.handleHideDelete}
                                        handleDelete={this.handleDelete}

                                    />


                                </CardBody>
                            </Card>
                        </Col>
                    </Row>
                </React.Fragment>

            </>

        )
    }
}

const mapStateToProps = ({ auth: { user } }) => ({
    user
});

export default (connect(mapStateToProps)(PaymentMode));